.header{
  height: 200px;
  width: 100%;
  background-color: #E9EEF6;
  padding-top: 50px;
}
.card-container{
  height: 300px;
  width: 300px;
  box-shadow: 0px 3px 50px #A5A5A5;
}
.card-wrapper{
  width: 232px;
  height: 245px;
  box-shadow: 0px 3px 18px #A5A5A5;
  display: flex;
  flex-direction: column;
}
.card-top{
  width:100%;
  height:2%;
}

.task-holder{
  width:100%;
  height:98%;
  padding:10px 10px;
  display: flex;
  flex-direction: column;
  position: relative;
}

.card-header{
  margin-top: 10px;
  margin-bottom: 10px;
  max-width: auto;
  height: 30px;
  padding: 1px 1px !important;
  text-align: center;
}

.task-container{
  height: 600px;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  background-color: #F6F7F8;
  padding:40px 100px;
  
}

@media only screen and (max-width: 800px){
  .task-container{
    align-items: center;
  }
 
  
  
}